import React, {useContext} from 'react'
import  './Mealitem.css'
import Mealitemforms from './Mealitemforms';
import { AuthContext } from '../../../../shared/context/auth-context';
import Card from '../../../../shared/components/UIElements/Card'
import { NavLink } from 'react-router-dom';

const Mealitem = (props) => {
const cartctx= useContext(AuthContext)

    // const prices =`$${props.price.toFixed(2)}`

    const addtocarthandler = amount =>{
cartctx.additem({
  id: props.id,
  name:props.name,
  title:props.title,
  productcreator:props.productcreator,
  description:props.description,
  image:props.image,
  amount:amount,
  mrp:props.mrp,
  price:props.price
})
    }

  return (
   <>    
    <div className='product_div'>
    
    <div className='form_input_para'>
    
<NavLink to= {`${props.id}/singleproduct`} style={{textDecoration:"none", color:"black"}}>
    <img src={`${process.env.REACT_APP_ASSET_URL}/${props.image}`} alt={props.title} />
    </NavLink>
    </div>
    <div className='container_para '>
<h1 style={{fontSize:"15px"}}>{props.title}</h1>
{/* <p style={{display:"flex"}}>{props.description}</p> */}
<h3 style={{fontSize:"15px", color:"red", textDecoration: "line-through"}}><span>Mrp: </span>{props.mrp}</h3>
<h3 style={{fontSize:"15px"}}><span>Offer Price: </span>{props.price}</h3></div>

<div className='form_input_div'>          
<Mealitemforms onaddtocart={addtocarthandler}/>
        </div>
</div>
   </>
  )
}

export default Mealitem
